<template>
    <div class="me">
      <!-- 顶部信息显示 -->
      <div class="Tops">
          <span class="iconfont icon-liaotianchuangkou myicon" @click='lll'></span>
          <div class="hears">
              <img :src="useObj.userImg">
              <span>
                  {{useObj.nickName}}
                  <van-icon name="arrow" />
              </span>
          </div>
          
      </div>
      <!-- 我的多功能区域 -->
      <van-grid :column-num="5" :border="false">
        <van-grid-item v-for="(item,index) in newDates" :key="index"   :border="false" @click="ff_goPages(item.router)">
            <template>
                <span class="iconfont myis" :class="item.iconSrc"></span>
                <span class="myTet">{{item.name}}</span>
            </template>
        </van-grid-item>
       <van-icon @click="ff_isUp" class="arrow" :name="isUp ? 'arrow-up' : 'arrow-down'" />
      </van-grid>

      <!-- 为你推荐 -->
      <Guess class="meGuess" :newDates="newLis" @gai="ff_gai($event)">
          <template #tile>
                    <span class="tetx">为你推荐</span> 
               </template>
      </Guess>
      
      <!-- 广告图片 -->
      <div class="haibao">
          <img @click="ff_goPages(Item.name)" v-for="(Item,index) in imgArr" :key="index" :src="Item.src">
      </div>


      <!-- 商品列表 -->
        <van-tabs class="myTabs" v-model="activeName"   sticky  animated background="#f5f5f3"  line-width="25px" >
            <van-tab v-for="(item,index) in goodsData"  :title="item.name" :key="index" title-style='font-size: 13px;'>
                <ul class="comLists">
                    <li v-for="(item,index) in goodsLits" :key="index" class="coms" @click="ff_goPages('Details',item.pid)">
                          <img :src="item.smallImg"/>
                          <div>
                              <p>{{item.name}}</p>
                              <p>
                                  <span>￥{{item.price}}</span> 
                                  <span>￥{{item.original}}</span>
                              </p>
                              <span v-if="item.isTale">满减</span>
                              <p>已售卖{{item.sales | ff_Pain}}件 <span class="icon"></span></p>
                          </div>

                    </li>
                </ul>
                
            </van-tab>
            <van-tab title="更多>" title-style='font-size: 12px;' disabled  @click="ff_goPages('Menu')"></van-tab>
        </van-tabs>
    </div>
</template>

<script>
    import Guess from '../components/Guess.vue'
    import '../assets/font/iconfont.css'
    import '../assets/less/me.less'
    
    
    export default {
        name:'Me',
        data() {
            return {
                useObj:{

                },
                flexoDates:[
                    {router:"Amend",name:'我的',iconSrc:'icon-ziliao'},
                    {router:"Order",name:'我的订单',iconSrc:'icon-dingdan'},
                    {router:"Collect",name:'我的收藏',iconSrc:'icon-shoucang'},
                    {router:"Topup",name:'代金券',iconSrc:'icon-youhuiquan'},
                    {router:"Coupon",name:'礼品卡',iconSrc:'icon-ka'},
                    {router:null,name:'账户余额',iconSrc:'icon-qiandai'},
                    {router:null,name:'发票管理',iconSrc:'icon-fapiao'},
                    {router:"Alliance",name:'兑换优惠',iconSrc:'icon-gift'},
                    {router:null,name:'帮助反馈',iconSrc:'icon-erji'},
                    {router:'Alliance',name:'瑞划算',iconSrc:'icon-VRxinlingshou'},
                ],
                newDates:  [],
                isUp    :  true,
                newLis  :  [],
                imgArr  :  [{src:require('../assets/img/me_01.png'),name:null},
                            {src:require('../assets/img/me_02.png'),name:'Coupon'}],
                activeName:'coffee',
                goodsData:[
                    {
                        type : 'coffee',
                        name : '咖啡饮品'
                    },
                    {
                        type : 'faddish',
                        name : '爆款周边'
                    },
                    {
                        type : 'snacks',
                        name : '冲调零食'
                    },
                    {
                        type : 'work',
                        name : '舒适办公'
                    },
                    
                ],
                goodsLits:[]
            }
            
        },
        methods: {
            ff_isUp(){
                this.isUp = !this.isUp;
                if(this.isUp){
                    this.newDates = this.flexoDates;
                }else{
                    this.newDates = this.flexoDates.slice(0,5);
                }
            },
             //  更改猜你喜欢数据
            ff_gai(obj){
                this.ff_Like(obj.key,obj.type)

            },
            // 猜你喜欢数据请求
            ff_Like(key='isHot',value=1){
                    this.axios({
                            url:'/typeProducts',
                            method:'GET',
                            params:{
                                appkey : this.appkey,
                                key,
                                value
                            }
                            })
                            .then(res=>{
                                this.newLis = res.data.result.slice(0,3);
                            })
                            .catch(err=>{
                                 
                            })
            },
            // 加载商品
            ff_upload(){
                    this.axios({
                        url:'/typeProducts',
                        method:'GET',
                        params:{
                            appkey:this.appkey,
                            key:'type',
                            value:'coffee',
                        }
                    })
                    .then(res=>{
                         this.goodsLits = res.data.result;
                         this.goodsLits.map(item=>{
                            item.sales = Math.floor(Math.random()*(7000-3000)+3000);
                            item.isTale = Math.random() >= 0.5 ? 'true' : 'false';
                            item.original = parseInt(item.price) + 20;
                        })
                        
                        
                   
                    })
                    .catch(err=>{
                         
                    })
            },
             // 跳转模块
            ff_goPages(name,pid=null){
                if(name || pid){
                       this.$router.push({name,query:{pid}});
                }else{
                    this.$toast.loading({
                        message: '   维护中  ',
                        forbidClick: true,
                        loadingType: 'spinner',
                    });
                }
                
            },
            lll(){
                this.$toast('surprised！')
            }

        },
        created() {
           
             this.$toast.loading({
                        message: '加载中...',
                        // 禁止点击背景
                        forbidClick: true,
                        loadingType: 'spinner',
            });
            
             this.useId = window.localStorage.getItem('useId');

             if(this.useId){
                    // 初始化用户
                    // this.useObj 
                    this.axios({
                        url:'/findMy',
                        method:'GET',
                        params:{
                            appkey      : this.appkey,
                            tokenString : this.useId,
                        }
                    })
                    .then(res=>{
                        if(res.data.code == 'A001'){ 
                            this.useObj = res.data.result[0];
                        }
                    })
                    .catch(err=>{
                         
                    })
                    
                    // 多功能区的数据初始化
                    this.newDates = this.flexoDates;
                    
                    // 请求猜你喜欢数据
                    this.ff_Like();

                    // 加载商品
                    this.ff_upload();

             }else{
                    this.$toast('请先登录');
                    this.$router.push({name:'Login'})
                 
             }


            


        },
       
        // 过滤器
        filters:{
            ff_Pain(v){
                let pain =`${v.toString().slice(0,2)}00+`;
                return pain;
            }
        },
        components:{
            Guess,
        }
    }
</script>

<style lang="less" scoped>
   
</style>